import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const ErrorMsg = ({ connectedAccounts, feeds }) => {
    const [isTwitter, setIsTwitter] = useState(false);
    const [isFeed, setIsFeed] = useState(false);
    useEffect(() => {
        const Twit_acc = connectedAccounts?.some((item) => item.ConnectedAccounts.networkId === 1 && item.ConnectedAccounts.is_verified === 0);
        if (Twit_acc) setIsTwitter(true);

        const spec_feed = feeds?.some((item) => item.Feed.api === 3 || item.Feed.api === 4);
        if (spec_feed) setIsFeed(true);
    }, [connectedAccounts, feeds]);

    return (
        <>
            {isTwitter && !isFeed ? <div className="review-bnn position-relative"> Refresh Your Twitter Wall To Ensure Smooth Content Sync. <Link className="link-primary ms-2" to={`/transactions#connectedAccounts`}><span className="rbnn-clicable">Click here to do it now!</span></Link> </div> : null}
            {feeds?.length && isFeed ? <div className="review-bnn position-relative"> ⚠️ Alert! Feed disconnected. Connect your social account to resume updates. <Link className="link-primary ms-2" to={`/transactions#connectedAccounts`}>Connect Now!!</Link> </div> : null}

        </>
    );
};

export default ErrorMsg;